import Vue from 'vue'
import Router from 'vue-router'
import VueRouter from "vue-router";
Vue.use(Router)
const routes = [
  {
    path: "/",
    name: "Home",
    meta: {
      requireAuth: true
    },
    component: () => import(/* webpackChunkName: "about" */ "../views/Home")
  },
  {
    path: "/project",
    name: "Project",
    meta: {
      requireAuth: true
    },
    component: () => import(/* webpackChunkName: "about" */ "../views/Project")
  },
  {
    path: "/other",
    name: "other",
    meta: {
      requireAuth: true
    },
    component: () => import(/* webpackChunkName: "about" */ "../views/Other")
  },
  /*
  * 文档展示页面
  * */
  // {
  //   path: "/Document",
  //   name: "Document",
  //   meta: {
  //     requireAuth: true
  //   },
  //   component: () => import(/* webpackChunkName: "about" */ "../views/Document"),
  //   children : [
  //   ]
  // },
  {
    path: '/*',
    name: 'error-404',
    meta: { title: '页面走丢啦~' },
    component: () => import(/* webpackChunkName: "about" */ "../views/notFound404"),
  },
];

const router = new VueRouter({
  routes,
  mode: 'hash'
});

//解决同名路由的跳转问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router;