import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import 'element-ui/lib/theme-chalk/index.css';
import 'lib-flexible'
import '../src/assets/font/font.css'
import VueI18n from 'vue-i18n'
import './style/index.less'
import './css/code.css'

import Highlight from './assets/highlight.js'; // from 路径是highlight.js的路径，纯属自定义
Vue.use(Highlight);

Vue.use(ElementUI);
Vue.config.productionTip = false;
Vue.use(VueI18n);

/**
 * 语言切换组件
 * @type {{en: {message: {hello: string}}, cn: {message: {hello: string}}}}
 */
const messages = {
    en: require('./utils/lang/en'),
    cn: require('./utils/lang/cn')
}
// 通过选项创建 VueI18n 实例
const i18n = new VueI18n({
    locale: 'cn', // 设置地区
    messages, // 设置地区信息
})

new Vue({
    router,
    i18n,
    render: h => h(App)
}).$mount("#app");


